<template>
    <div class="container">
        <div class="myOrder">
            <h1 class="myOrder__heading">Мой заказ</h1>
            
            <span class="myOrder__table">Стол 8</span>
        </div>

        <div class="dishList">
            <div class="divide__check" v-if="dishItem && notPaid != 0">
                <span class="divide__check-label">Разделить счёт</span>

                <label class="divide__check-switch">
                    <input type="checkbox" v-model="diffCheck" :disabled="notPaid == 1" @click="diffCheckFunc">

                    <span class="slider"></span>
                </label>
            </div>

            <div class="emptyList" v-if="!dishItem">
                <img src="@/assets/images/clock_logo.svg" alt="Изображение часов" class="emptyList__icon">
                
                <h3 class="emptyList__heading">Скоро здесь появится ваш заказ</h3>
                
                <!-- <p class="emptyList__explanation">Возможно, официант ещё не успел открыть заказ пожалуйста, подождите..</p> -->
            </div>

          <div class="emptyList" v-else-if="notPaid == 0">
            <img src="@/assets/images/clock_logo.svg" alt="Изображение часов" class="emptyList__icon">

            <h3 class="emptyList__heading">Заказ успешно оплачен</h3>

            <!-- <p class="emptyList__explanation">Возможно, официант ещё не успел открыть заказ пожалуйста, подождите..</p> -->
          </div>

            <ul class="dishList__list" v-else>
                <li class="dishList__item" :class="{ 'paid': item.pay}" v-for="(item,  index) in items" :key="item.id">
                    <p class="dishList__item-status" v-if="!item.pay && diffCheck">Не оплачено</p>
                    <p class="dishList__item-status" v-else-if="!item.pay && !diffCheck"></p>
                    <p class="dishList__item-status" v-else>Оплачено</p>

                    <div class="dishList__item-dish">
                        <label class="dish__checkbox">
                            <input type="checkbox" v-model="item.active" @click="checkPosition()" :disabled="item.active && item.pay" >

                            <span v-if="diffCheck"></span>

                            <p class="dish__checkbox-text">{{item.name}}</p>
                        </label>

                        <div class="dishList__item-total">
                            <span class="sum">{{item.price * item.count}} &#8381;</span>

                            <div class="count">
                                <button class="count__btn minus" @click="minus(index)" :disabled="item.pay || !diffCheck">
                  <svg width="10" height="4" viewBox="0 0 10 4" fill="none" xmlns="http://www.w3.org/2000/svg" :style="{'visibility' : !diffCheck ? 'hidden' : 'visible'}">
                                        <path d="M0 3.25V0.75C0.910053 0.75 1.78307 0.75 2.61905 0.75C3.46561 0.75 4.25926 0.75 5 0.75C5.74074 0.75 6.53439 0.75 7.38095 0.75C8.22751 0.75 9.10053 0.75 10 0.75V3.25C9.10053 3.25 8.22751 3.25 7.38095 3.25C6.53439 3.25 5.74074 3.25 5 3.25C4.25926 3.25 3.46561 3.25 2.61905 3.25C1.78307 3.25 0.910053 3.25 0 3.25Z" fill="#808697"/>
                                    </svg>
                                </button>

                                <span class="count__quantity">{{ item.count }}</span>

                                <button class="count__btn" @click="plus(index)" :disabled="item.pay || !diffCheck || item.count == item.maxCount" >
                  <svg width="10" height="10" viewBox="0 0 10 10" fill="none" xmlns="http://www.w3.org/2000/svg" :style="{'visibility' : !diffCheck ? 'hidden' : 'visible'}">
                                        <path d="M3.8208 10C3.8208 9.32728 3.8208 8.6781 3.8208 8.05247C3.8208 7.42011 3.8208 6.83148 3.8208 6.28658V3.72351C3.8208 3.17188 3.8208 2.58325 3.8208 1.95762C3.8208 1.32526 3.8208 0.672721 3.8208 0H6.1689C6.1689 0.672721 6.1689 1.32526 6.1689 1.95762C6.1689 2.58325 6.1689 3.17188 6.1689 3.72351V6.28658C6.1689 6.83148 6.1689 7.42011 6.1689 8.05247C6.1689 8.6781 6.1689 9.32728 6.1689 10H3.8208ZM0 6.12513V3.88496C0.686577 3.88496 1.34912 3.88496 1.98764 3.88496C2.63302 3.88496 3.23378 3.88496 3.78991 3.88496H6.19979C6.76279 3.88496 7.36354 3.88496 8.00206 3.88496C8.64744 3.88496 9.31342 3.88496 10 3.88496V6.12513C9.31342 6.12513 8.64744 6.12513 8.00206 6.12513C7.36354 6.12513 6.76279 6.12513 6.19979 6.12513H3.78991C3.23378 6.12513 2.63302 6.12513 1.98764 6.12513C1.34912 6.12513 0.686577 6.12513 0 6.12513Z" fill="#808697"/>
                                    </svg>
                                </button>
                            </div>
                        </div>
                    </div>
                </li> 
            </ul>
        </div>
        <div v-if="dishItem && notPaid != 0">
        <div class="tips">
            <span class="tips__heading">Чаевые</span>
                
            <div class="tips__waiter">
                <img src="@/assets/images/image_waiter.jpg" alt="Фото официанта">

                <div class="tips__waiter-info">
                    <span>Официант</span>

                    <span>Наталья</span>
                </div>
            </div>

            <div class="tips__sum">
                <span>{{tips}}</span> &#8381;
                <span class="custom-input">
                    <input type="tel" v-model.number="tips" @click="manualTips = true"></span>
                    <img class="close-input" src="@/assets/images/close_icon.svg" v-if="inputTips" @click="showInputTips" alt="Закрыть">   
                <button class="tips__sum-btn" @click="resetTips" type="reset">
                    <img src="@/assets/images/close_icon.svg" alt="Закрыть">
                </button>
            </div>

            <div class="tips__percents">
                <ul class="tips__percents-list">
                    <li class="tips__list-item" :class="{ 'active': percents == 0 && !manualTips}" @click="selectPercents(0)">0%</li>
                        
                    <li class="tips__list-item" :class="{ 'active': percents == 10 && !manualTips}" @click="selectPercents(10)">10%</li>
                        
                    <li class="tips__list-item" :class="{ 'active': percents == 15 && !manualTips}" @click="selectPercents(15)">15%</li>
                        
                    <li class="tips__list-item" :class="{ 'active': percents == 20 && !manualTips}" @click="selectPercents(20)">20%</li>
                        
                    <li class="tips__list-item" :class="{ 'active': percents == 25 && !manualTips}" @click="selectPercents(25)">25%</li>
                </ul>
            </div>
        </div>

        <div class="check">
            <a href="#" class="check__discount" @click.prevent="showEnterPhone">Применить скидку</a>

            <div class="check__total" v-if="diffCheck">
                <span class="check__total-heading">Общий счёт:</span>
                
                <span class="check__total-sum">{{summaAll}} &#8381;</span>
            </div>

            <div class="check__position">
                <div class="check__marked" v-if="diffCheck">
                    <span class="check__tips-heading">Отмеченные позиции:</span>
                
                    <span class="check__tips-sum">{{ summaChecked }} &#8381;</span>
                </div>

                <div class="check__tips">
                    <span class="check__tips-heading">Чаевые:</span>
                
                    <span class="check__tips-sum">{{tips}} &#8381;</span>
                </div>
            </div>

            <div class="check__totalPay">
                <span class="check__totalPay-heading">Итого к оплате:</span>
                
                <span v-if="diffCheck">{{ summaChecked + tips + (checkTransaction ?  summTransaction : 0)}} &#8381;</span>
                <span v-if="!diffCheck">{{ summaAll + tips + (checkTransaction ?  summTransaction : 0) }} &#8381;</span>
            </div>
        </div>
    
        <form action="" class="payment" @submit.stop.prevent="prevent">
            <button class="payment-btn main-btn" @click="showChoicePayment(true)" :disabled="(summaChecked == 0 && diffCheck) || (pay && summaChecked == 0)">Оплатить</button>
        
            <button class="share-btn main-btn" @click="showShareCheck(true)" :disabled="diffCheck && summaChecked == 0 ">Поделиться счётом</button>
            <label class="payment__conditions"> 
                <input type="checkbox" checked class="payment__conditions-check" v-model="checkTransaction">
                <p class="payment__conditions-text">Я хочу взять на себя транзакционные издержки ({{transactionsSumm}} ₽), чтобы сотрудник получил полную сумму</p>
            </label>
            <label class="payment__conditions">
                <input type="checkbox" checked class="payment__conditions-check">
                <div class="payment__conditions-text">
                    <p>Согласен с условиями 
                        <a href="#">Пользовательского соглашения</a> и 
                    
                        <a href="#">Политикой обработки персональных данных</a>
                    </p>
                </div>
            </label>  
        </form>        
    </div>
    </div>
    <div class="menu">
        <div class="callWaiter__text" v-if="!dishItem">
            <span>Вызвать официанта</span>
            <svg width="12" height="16" viewBox="0 0 12 16" fill="none" xmlns="http://www.w3.org/2000/svg">
                <path d="M11.001 9.99998L6.00001 15.001L0.999009 9.99998M6.00001 15L6.00001 0.999977L6.00001 15Z" stroke="#343D5D" stroke-width="1.5" stroke-linecap="round" stroke-linejoin="round"/>
            </svg>
        </div>
        
        <div class="callWaiter" @click="showCallWaiter(true)">
            <img src="~@/assets/images/waiter_icon.svg" alt="">
        </div>
        
    </div>

    <div>
        <choicepayment v-if="choicePaymentVisible" @showChoicePayment="showChoicePayment" @showPayment="showPayment"/>      
        <callwaiter v-if="callWaiter" @showCallWaiter="showCallWaiter"/>
        <payment v-if="paymentVisible" @showPayment="showPayment" @showPaidCheck="showPaidCheck" @returnItem="returnItem" :sum="summaPay" :item="items" :diffCheck="diffCheck"/>    
        <sharecheck v-if="shareCheckVisible" @showShareCheck="showShareCheck"/>   
        <review v-if="reviewVisible" @showReview="showReview"/>
        <paidcheck v-if="paidCheckVisible" @showPaidCheck="showPaidCheck" @showReview="showReview"/>
        <enterphone v-if="enterPhoneVisible" @showEnterPhone="showEnterPhone" @showEnterCode="showEnterCode" />
        <entercode v-if="enterCodeVisible" @showEnterCode="showEnterCode" />
    </div>
</template>

<script>
import Choicepayment from "@/components/Choiсepayment"
import Callwaiter from "@/components/Callwaiter"
import Payment from "@/components/Payment"
import Sharecheck from "@/components/Sharecheck"
import Review from "@/components/Review"
import Paidcheck from "@/components/PaidCheck"
import Enterphone from "@/components/EnterPhone"
import Entercode from "@/components/EnterCode"
    export default {
        name: 'Order',
        components: {
            Choicepayment,
            Callwaiter,
            Payment,
            Sharecheck,
            Review,
            Paidcheck,
            Enterphone,
            Entercode
        },

        data () {
            return {
                dishItem: false,
                choicePaymentVisible: false,
                callWaiter: false,
                paymentVisible: false,
                shareCheckVisible: false,
                reviewVisible: false,
                pay : false,
                percents: 10,
                summaAll : 0,
                summaChecked : 0,
                summaPay : 0,
                diffCheck : false,
                inputTips : false,
                notPaid : 0,
                manualTips : false,
                checkTransaction : true,
                summTransaction : 0,
                items: [   
                    {id:0, name: "Тыквенный суп", price: 450, payed : false, active : false, count: 2, pay : false, maxCount : 2},
                    {id:1, name: "Морские гребешки с соусом", price: 700, payed : false, active: false, count: 1, pay : false, maxCount : 1},
                    {id:2, name: "Паста болоньезе с соусом песто", price: 375, payed : false, active: false, count: 1, pay : false, maxCount : 1},
                    {id:3, name: "Чизкейк", price: 250, payed : false, active: false, count: 2, pay : false, maxCount : 2},
                    {id:4, name: "Да лука игристое розовое", price: 1200, payed : false, active: false, count: 1, pay : false, maxCount : 1}
                ],
                tips : 0,
                paidCheckVisible: false,
                enterPhoneVisible: false,
                enterCodeVisible: false
            }
        },

        methods: {
            showChoicePayment (val) {
                this.choicePaymentVisible = val
                this.recount();
               },
            showInputTips () {
                this.inputTips = !this.inputTips;
                if (this.tips == '') this.tips = 0;
            },
            showCallWaiter (val) {
                this.callWaiter = val
            },

            showPayment(val) {
                this.choicePaymentVisible = false
                this.paymentVisible = val
                setTimeout(() => {
                    this.paymentVisible = false 
                    this.paidCheckVisible = true
                }, 2000);
            },
            returnItem (val) {
              this.items = val;

            },

            showShareCheck(val) {
                this.shareCheckVisible = val
            },

            selectPercents (val) {
                this.percents = val
                if (this.diffCheck) {
                this.tips = Math.round(val * this.summaChecked / 100);
                }
                else {
                  this.tips = Math.round(val * this.summaAll / 100);
                }
                this.manualTips = false
            },

            showReview(val) {
                this.paidCheckVisible = false
                this.reviewVisible = val
                this.recount();
                this.pay = true;
              this.percents = 0;
            },

            LoadDishList() {
                setTimeout(() => {
                    this.dishItem = true
                    this.recount();
                }, 3000);
            },
            checkPosition () {
              setTimeout(() => {
                this.recount ();
              }, 300);

            },
            plus (id) {
              this.items[id].count += 1;
              this.recount ();
            },
            minus (id) {
              if (this.items[id].count >1) {
                this.items[id].count -= 1;
                this.recount ();
              }
            },
            diffCheckFunc () {
              this.recount();
              this.resetTips();
            },
            recount () {
              // this.resetTips();
              let summa = 0;
              this.notPaid = 0;
              let self = this;
              this.items.forEach( function (item) {
                if (!item.pay) {
                  summa = summa + item.price * item.count;
                  self.notPaid += 1
                }

                // this.summaAll = this.summaAll + item.price
              });

              this.summaAll = summa;
              let summaChecked = 0;


              let aa = this.items.filter(item => item.active)
              aa.forEach( function (item) {
                if (!item.pay) {
                  summaChecked = summaChecked + item.price * item.count;
                }
              });
              this.summaChecked = summaChecked;


              
                if (!this.diffCheck) {
                    this.summaPay = this.summaAll;
                }
                else {
                    this.summaPay = this.summaChecked
                }

              if (!this.manualTips) {
                this.tips = Math.round(this.percents * this.summaPay / 100);
              }
            },
            resetTips () {
              this.percents = 0;
              this.tips = 0;
            },

            showPaidCheck(val) {
                this.paymentVisible = false
                this.paidCheckVisible = val
            },

            showEnterPhone(val) {
                this.enterPhoneVisible = val
            },
            
            showEnterCode(val) {
                this.enterPhoneVisible = false
                this.enterCodeVisible = val
            }
        },

        mounted() {
            this.LoadDishList() 
            this.recount()
        },
        computed: {
          transactionsSumm: function () {
            let transactionTips = 0;
            let transactionMenu  = 0;

              transactionTips = this.tips * 5/100;
              if (!this.diffCheck) {
                transactionMenu = this.summaAll * 1.4 / 100;
              }
              else {
                transactionMenu = this.summaChecked * 1.4 / 100;
              }


            this.summTransaction = transactionTips + transactionMenu;
            return transactionTips + transactionMenu;
          }

        },
        watch () {

        }
    }
</script>

<style lang="scss">
    .myOrder {
        display: flex;
        justify-content: space-between;
        margin-bottom: 24px;

        font-weight: 600;
        font-size: 20px;
        line-height: 25px;

        &__heading {
            font-weight: 600;
            font-size: 20px;
            line-height: 25px;
        }

        &__table {
           color: #80869A; 
        }
    }

    .dishList__list {
        & .paid {
            border: 1px solid #E6E7EB;
            color: #CCCFD7;

            & .dishList__item-status {
                color: #25C26E;
            }

            & .dish__checkbox-text {
                text-decoration: line-through;

                &:before {
                    border-color: #DCDEE3;
                }
            }

            & .sum {
                color: #CCCFD7;
                text-decoration: line-through;
            }

            & .count {
                opacity: 0.5;
            }
        }
    }

    .divide__check {
        display: flex;
        justify-content: space-between;
        align-items: center;
        padding: 12px;

        border: 1px solid #E6E7EB;
        border-radius: 11px;

        background-color: #fff;

        &-label {
            font-weight: 600;
            font-size: 17px;
            line-height: 22px;
            color: #494C55;
        }

        &-switch {
            position: relative;
            display: inline-block;
            min-width: 48px;
            height: 24px;

            & input {
                display: none;

                &:checked + .slider {
                    background-color: #6764FF;
                }

                &:checked + .slider::before {
                        transform: translateX(23px);
                }
            }
        }

        & .slider {
            position: absolute;
            top: 0;
            left: 0;
            right: 0;
            bottom: 0;
            background-color: #E6E7EB;
            transition: .4s;
            border-radius: 50px;

            &::before {
                position: absolute;
                content: '';
                width: 19px;
                height: 19px;
                left: 3px;
                bottom: 3px;
                background-color: #fff;
                transition: .4s;
                border-radius: 50%;
            }
        }
    }

    .dishList__item {
        margin-top: 10px;
        padding: 12px;

        border: 1px solid #E6E7EB;
        border-radius: 12px;
        list-style-type: none;

        background-color: #fff;

        &.paid {
            & .dishList__item-dish {
                & .dish__checkbox {
                    & input:checked+span {
                        background-image: url('~@/assets/images/checked_paid.svg');
                    }
                }
            }
            
        }

     
        &-status {
            font-weight: 500;
            font-size: 13px;
            line-height: 18px;
            text-align: center;

            color: #80869A;
        }

        &-dish {
            display: flex;
            justify-content: space-between;

            & .dish__checkbox {
                display: flex;
                align-items: center;

                & span {
                    content: '';
                    display: inline-block;
                    width: 14px;
                    height: 14px;
                    flex-shrink: 0;
                    flex-grow: 0;
                    border: 1.5px solid #DCDEE3;
                    border-radius: 0.25em;
                    margin-right: 21px;
                    background-repeat: no-repeat;
                    background-position: center center;
                    background-size: 12px;
                }

                & input {
                    position: absolute;
                    z-index: -1;
                    opacity: 0;

                    &:checked+span {
                        background-image: url('~@/assets/images/checked.65527f52.svg');
                    }
                }

                &-text {
                    display: inline-flex;
                    align-items: center;
                    max-width: 170px;

                    font-size: 16px;
                    line-height: 20px;
                }
            }
        }

        &-total {
            display: flex;
            flex-direction: column;

            font-weight: 500;
            font-size: 16px;
            line-height: 20px;
            text-align: right;

            color: #413DFF;


            & .count {
                display: flex;
                align-items: center;
                margin-top: 8px;
                padding: 3px 7px;

                border: 1px solid #EDEEF1;
                border-radius: 6px;
                background: #F7F8FA;

                font-weight: normal;
                font-size: 13px;
                line-height: 16px;

                &__quantity {
                    font-size: 13px;
                    line-height: 18px;

                    color: #80869A;
                }

                &__btn {
                    padding: 0;

                    outline: none;
                    border: none;
                    background-color: inherit;
                    cursor: pointer;

                    & svg {
                        vertical-align: middle;
                    }
                }

                & span {
                    margin: 0 11px;
                }
            }
        }
    }  
    
    .emptyList {
        margin-top: 130px;
        margin-bottom: 31px;
        padding: 17px 0 ;

        text-align: center;

        &__icon {
            margin-bottom: 18px;
        }

        &__heading {
            margin: 0 auto;
            margin-bottom: 16px;
            max-width: 298px;
            
            font-weight: 600;
            font-size: 24px;
            line-height: 30px;
        }

        // &__explanation {
        //     margin: 0 auto;
        //     max-width: 279px;

        //     font-weight: 500;
        //     font-size: 16px;
        //     line-height: 20px;

        //     color: #343D5D;
        // }        
    }

    

    .tips {
        margin-top: 24px;
        margin-bottom: 24px;
        padding: 11px;

        border: 1px solid #E6E7EB;
        border-radius: 11px;

        background-color: #fff;

        &__heading {
            font-weight: 600;
            font-size: 17px;
            line-height: 22px;
        }

        &__waiter {
            margin-top: 16px;
            margin-bottom: 13px;

            & img {
                margin-right: 12px;
            }

            &-info {
                display: inline-flex;
                flex-direction: column;

                vertical-align: top;
                
                font-weight: 400;
                font-size: 13px;
                line-height: 18px;
                color: #343D5D;

                & span:first-child {
                    color: #80869A;
                }
            }
        }

        &__sum {
            position: relative;

            padding-bottom: 19px;

            font-weight: 600;
            font-size: 17px;
            line-height: 22px;

            text-align: center;

            border-bottom: 1px solid #EDEEF1;

            & .custom-input {
                position: absolute;
                left: 50%;

                transform: translateX(-50%);
                

                & input {
                    width: 40px;

                    font-weight: 600;
                    font-size: 17px;
                    line-height: 22px;

                    opacity: 0;
                }
            }

            & .close-input {
                position: absolute;
                right: 0;
                z-index: 2;
            }

            &-btn {
                position: absolute;
                right: 0;
                top: 0;

                width: 24px;
                height: 24px;
                display: flex;
                justify-content: center;
                align-items: center;

                outline: none;
                background: #F7F8FA;
                cursor: pointer;

                border-radius: 50%;
                border: 1px solid #EDEEF1;
            }
        }

        &__percents {
            padding-top: 18px;

            &-list {
                display: flex;
                justify-content: space-between;
                list-style-type: none;

                & .tips__list-item {
                    padding: 4px;

                    font-weight: 500;
                    font-size: 16px;
                    line-height: 20px;
                    color: #80869A;
                    cursor: pointer;

                    background: #F7F8FA;
                    border-radius: 6px;
                    border: 1px solid #F7F8FA;

                    &:hover {
                        color: #010D35;
                        border: 1px solid #6764FF;
                    }
                }

                & .active {
                    color: #010D35;
                    border: 1px solid #6764FF;
