<template>
    <div class="modal__overlay" @click="showReview"></div>
    <div class="review">
        <div class="container">
            <h2 class="review__title">Вам всё понравилось?</h2>

            <div class="review__stars">
                <div class="review__stars-item" style="display: none" :class="{ 'active': rating == 0}" @click="handleRating(1)">
                    <svg width="21" height="19" viewBox="0 0 21 19" xmlns="http://www.w3.org/2000/svg">
                        <path d="M10.5 15.235L4.679 18L5.888 11.88L1.5 7.392L7.679 6.621L10.5 1L13.321 6.621L19.5 7.392L15.112 11.88L16.321 18L10.5 15.235Z" stroke-width="1.5" stroke-linecap="round" stroke-linejoin="round"/>
                    </svg>
                </div>
                <div class="review__stars-item" :class="{ 'active': rating == 1}" @click="handleRating(1)">
                    <svg width="21" height="19" viewBox="0 0 21 19" xmlns="http://www.w3.org/2000/svg">
                        <path d="M10.5 15.235L4.679 18L5.888 11.88L1.5 7.392L7.679 6.621L10.5 1L13.321 6.621L19.5 7.392L15.112 11.88L16.321 18L10.5 15.235Z" stroke-width="1.5" stroke-linecap="round" stroke-linejoin="round"/>
                    </svg>
                </div>
                <div class="review__stars-item" :class="{ 'active': rating == 2}" @click="handleRating(2)">
                    <svg width="21" height="19" viewBox="0 0 21 19" xmlns="http://www.w3.org/2000/svg">
                        <path d="M10.5 15.235L4.679 18L5.888 11.88L1.5 7.392L7.679 6.621L10.5 1L13.321 6.621L19.5 7.392L15.112 11.88L16.321 18L10.5 15.235Z" stroke-width="1.5" stroke-linecap="round" stroke-linejoin="round"/>
                    </svg>
                </div>
                <div class="review__stars-item" :class="{ 'active': rating == 3}" @click="handleRating(3)">
                    <svg width="21" height="19" viewBox="0 0 21 19" xmlns="http://www.w3.org/2000/svg">
                        <path d="M10.5 15.235L4.679 18L5.888 11.88L1.5 7.392L7.679 6.621L10.5 1L13.321 6.621L19.5 7.392L15.112 11.88L16.321 18L10.5 15.235Z" stroke-width="1.5" stroke-linecap="round" stroke-linejoin="round"/>
                    </svg>
                </div>
                <div class="review__stars-item" :class="{ 'active': rating == 4}" @click="handleRating(4)">
                    <svg width="21" height="19" viewBox="0 0 21 19" xmlns="http://www.w3.org/2000/svg">
                        <path d="M10.5 15.235L4.679 18L5.888 11.88L1.5 7.392L7.679 6.621L10.5 1L13.321 6.621L19.5 7.392L15.112 11.88L16.321 18L10.5 15.235Z" stroke-width="1.5" stroke-linecap="round" stroke-linejoin="round"/>
                    </svg>
                </div>
                <div class="review__stars-item" :class="{ 'active': rating == 5}" @click="handleRating(5)">
                    <svg width="21" height="19" viewBox="0 0 21 19" xmlns="http://www.w3.org/2000/svg">
                        <path d="M10.5 15.235L4.679 18L5.888 11.88L1.5 7.392L7.679 6.621L10.5 1L13.321 6.621L19.5 7.392L15.112 11.88L16.321 18L10.5 15.235Z" stroke-width="1.5" stroke-linecap="round" stroke-linejoin="round"/>
                    </svg>
                </div>
            </div>

            <ul class="review__list" v-if="rating > 3" >
                <li class="review__list-item" :class="{ 'active': reviewItem.active}" v-for="(reviewItem, index) in reviewItems" :key="reviewItem.id" @click="addActive(index)">
                    <img :src="require('../assets/images/' + reviewItem.image)" alt="">

                    <span>{{reviewItem.text1}}</span>

                    <span>{{reviewItem.text2}}</span>
                </li>
            </ul>

            <div class="suggestions" v-if="rating && rating <= 3 && rating >0 ">
                <p class="suggestions__title">В чём мы можем стать лучше?</p>

                <ul class="suggestions__list">
                    <li class="suggestions__list-item">
                        <label class="suggestions__list-checkbox">
                            <span>Обслуживание</span>

                            <input type="checkbox">

                            <span class="custom-checkbox"></span>
                        </label>
                    </li>

                    <li class="suggestions__list-item">
                        <label class="suggestions__list-checkbox">
                            <span>Чистота</span>

                            <input type="checkbox">

                            <span class="custom-checkbox"></span>
                        </label>
                    </li>

                    <li class="suggestions__list-item">
                        <label class="suggestions__list-checkbox">
                            <span>Атмосфера</span>

                            <input type="checkbox">

                            <span class="custom-checkbox"></span>
                        </label>
                    </li>

                    <li class="suggestions__list-item">
                        <label class="suggestions__list-checkbox">
                            <span>Качество блюд</span>

                            <input type="checkbox">

                            <span class="custom-checkbox"></span>
                        </label>
                    </li>
                </ul>
            </div>

            <p class="review__text">Комментарий</p>

            <textarea class="review__textarea" name="review" placeholder="Расскажите подробнее..."></textarea>

            <button class="main-btn" @click="showReview">Готово</button>
        </div>
    </div>
</template>

<script>
export default {
    name: 'Review',

    data() {
        return {
            rating: 0,
            review: "",
            reviewItems: [
                {id:0, image: "atmosfera.png", text1: "Прекрасная", text2: "Атмосфера", active: false},
                {id:1, image: "clean.png", text1: "Очень", text2: "Чисто", active: false},
                {id:2, image: "interier.png", text1: "Красивый", text2: "Интерьер", active: false},
                {id:3, image: "service.png", text1: "Великолепное", text2: "Обслуживание", active: false},
                {id:4, image: "dish.png", text1: "Изысканые", text2: "Блюда", active: false}
            ]
        }
    },

    methods: {
        handleRating(val) {
            this.rating = val
        },

        handleReview(val) {
            this.review = val
        },

        showReview() {
            this.$emit("showReview", false)
        },

        addActive(index) {
            this.reviewItems[index].active = !this.reviewItems[index].active
        }
    }
}
</script>

<style lang="scss">
    .review {
        position: fixed;
        bottom: 45px;

        padding-top: 12px;
        width: 100%;

        background: #fff;
        border-radius: 12px 12px 0px 0px;

        z-index: 2;

        &__title {
            margin-bottom: 15px;

            font-weight: 600;
            font-size: 20px;
            line-height: 25px;
            color: #010D35;
            text-align: center;
        }

        &__stars {
            display: flex;
            justify-content: space-between;
            width: 162px;
            margin: 0 auto;
            margin-bottom: 28px;

            &-item {
                & svg {
                    fill: #FFD140;
                    stroke: #FFD140;
                }

                &.active {
                    &~* {
                        svg {
                            fill: #EDEEF1;
                            stroke: #EDEEF1;
                        }
                    }
                }
            }
        }

        &__list {
            display: flex;
            margin-bottom: 24px;

            font-weight: 500;
            font-size: 13px;
            line-height: 18px;

            overflow-x: scroll;

            &-item {
                display: flex;
                flex-direction: column;
                align-items: center;
                margin-right: 16px;

                list-style-type: none;

                color: #80869A;

                & img {
                    opacity: 0.4;
                }

                &.active {
                    span {
                        color: #010D35;
                    }

                    Img {
                        opacity: 1;
                    }
                }
            }
        }

        .suggestions {

            &__title {
                margin-bottom: 12px;

                font-weight: 600;
                font-size: 17px;
                line-height: 22px;
                color: #343D5D;
                text-align: center;
            }

            &__list {
                margin-bottom: 24px;

                &-item {
                    margin-bottom: 12px;

                    font-weight: 500;
                    font-size: 14px;
                    line-height: 18px;
                    color: #343D5D;

                    list-style-type: none;

                    &:last-child {
                        margin-bottom: 0;
                    }

                    & .suggestions__list-checkbox {
                        display: flex;
                        justify-content: space-between;
                        align-items: center;

                        & input {
                            position: absolute;
                            z-index: -1;
                            opacity: 0;

                            &:checked+.custom-checkbox {
                                background-image: url('~@/assets/images/checked.65527f52.svg');
                            }
                        }

                        & .custom-checkbox {
                            content: '';
                            display: inline-block;
                            width: 14px;
                            height: 14px;
                            flex-shrink: 0;
                            flex-grow: 0;
                            border: 1.5px solid #DCDEE3;
                            border-radius: 0.25em;
                            margin-right: 21px;
                            background-repeat: no-repeat;
                            background-position: center center;
                            background-size: 12px;
                        }
                    }
                }
            }
        }

        &__text {
            padding-bottom: 8px;

            font-weight: 600;
            font-size: 17px;
            line-height: 22px;
            color: #343D5D;

            border-bottom: 1px solid #EDEEF1;
        }

        &__textarea {
            width: 100%;
            height: 120px;
            margin-bottom: 24px;

            border: none;
            outline: none;
            resize: none;

            font-size: 16px;

            @media (min-width: 768px) {
                font-size: 14px;
            }

            &::placeholder {
                font-family: Commissioner;
                font-weight: 400;
                font-size: 13px;
                line-height: 18px;
            }
        }

        // &__send-btn {
        //     width: 100%;
        //     padding: 16px 0;
        //     margin-bottom: 12px;

        //     font-weight: 600;
        //     font-size: 17px;
        //     line-height: 22px;
        //     color: #343D5D;

        //     background: #EDEEF1;
        //     border-radius: 12px;
        //     border: none;
        // }
    }
</style>